import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);
import SupporterTab from "@/components/SupporterTab";
import dashBoard from "@/components/dashBoard";
import managechatroom from "@/components/managechatroom";
import Login from "@/components/login/Login";
import SignUp from "@/components/login/SignUp";

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/SupporterTab",
      name: "SupporterTab",
      component: SupporterTab
    },
    {
      path: "/dashBoard",
      name: "dashBoard",
      component: dashBoard
    },
    {
      path: "/managechatroom",
      name: "managechatroom",
      component: managechatroom
    },
    {
      path: "/login",
      name: "Login",
      component: Login
    },
    {
      path: "/login/signUp",
      name: "SignUp",
      component: SignUp
    }
  ]
});



